/* Resets */
.division-topbar input[type="submit"] {
  -webkit-appearance: button;
  /* 2 */
  cursor: pointer;
  /* 3 */
}
.division-topbar input[type="search"] {
  -webkit-appearance: textfield;
  /* 1 */
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  /* 2 */
  box-sizing: border-box;
}
.division-topbar * {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.division-topbar *:before,
.division-topbar *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
.division-topbar img {
  vertical-align: middle;
  height: auto;
  max-width: 100%;
}
.division-topbar a {
  text-decoration: none;
}
.lt-ie9 .division-topbar a {
  width: 240px;
}
.clearfix,
.nav-links:after {
  *zoom: 1;
}
.clearfix:before {
  content: " ";
  display: table;
}
.clearfix:after,
.nav-links:after {
  content: " ";
  display: table;
  clear: both;
}
/* ====================================================== */
.wrapper {
  margin: 0 auto;
  max-width: 980px;
  position: relative;
}
.container {
  position: relative;
}
.division-topbar {
  background: #222;
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.5;
  position: relative;
}
.uiowa,
.directory-toggle {
  line-height: 43px;
}
.uiowa {
  display: block;
  float: left;
  opacity: .7;
  width: 177px;
}
.uiowa:hover {
  opacity: 1;
}
/* Toggles
========================================================= */
.division-topbar button {
  background-color: transparent;
  border: none;
  text-align: left;
  padding: 0;
}
.directory-toggle {
  float: right;
  width: 253px;
}
.search-toggle {
  display: none;
}
.directory-toggle:after {
  /* creates the arrow */
  content: '';
  border-style: solid;
  border-width: 8px 6px 0 6px;
  border-color: #666 transparent transparent transparent;
  display: inline-block;
  height: 0px;
  position: relative;
  left: 4px;
  text-decoration: none;
  width: 0px;
}
.directory-toggle.active:after {
  /* flips the arrow upside down when clicked */
  border-width: 0 5px 6px 5px;
  border-color: transparent transparent #777 transparent;
}
.directory-toggle img {
  display: inline-block;
}
/* Search
========================================================= */
.division-search {
  position: relative;
  position: absolute;
  right: 279px;
  bottom: 7px;
}
.division-search label {
  /* visually hides the label */
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
.division-search form {
  margin: 0;
  position: relative;
}
@media only screen and (min-width: 601px) {
  .division-search {
    display: block !important;
  }
}
.division-search-input,
input.division-search-input[type="search"] {
  -webkit-appearance: textfield;
  background: #fff;
  border: 1px solid #000;
  color: #222;
  display: inline-block;
  font-size: 13px;
  font-family: arial, verdana, sans-serif;
  height: auto;
  line-height: normal;
  padding: 5px 0 4px 5px;
  position: relative;
  vertical-align: middle;
  width: 100%;
  margin-bottom: 0;
  -webkit-border-radius: 15px;
  -moz-border-radius: 15px;
  border-radius: 15px;
  -webkit-transition: width 0.2s ease, background-color 0.2s ease;
  -moz-transition: width 0.2s ease, background-color 0.2s ease;
  transition: width 0.2s ease, background-color 0.2s ease;
}
@media only screen and (min-width: 601px) {
  .division-search-input,
  input.division-search-input[type="search"] {
    width: 120px;
  }
}
/* Focus */
.division-search-input:focus,
input.division-search-input[type="search"]:focus {
  outline: none;
  text-shadow: none;
  width: 180px;
}
[placeholder]:focus::-webkit-input-placeholder {
  transition: opacity 0.5s 0.5s ease;
  opacity: 0;
}
.division-search-btn {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
