.nav-wrapper {
	@media (max-width: $break-medium - 1) {
		display: none;
	}

	@media (min-width: $break-large) {
		float: right;
		padding-top: 20px;
	}
}

.main-nav, .main-nav ul {
	margin: 0;
	padding: 0;
	list-style: none;
	text-align: center;
}

.main-nav {
	padding-top: 15px;
}

.main-nav > li {
	display: inline-block;
	position: relative;
}

/* Top level nav items */
.main-nav > li > a {
	color: #fff;
	display: block;
	font-size: 20px;
	font-weight: bold;
	padding: 10px 15px;
	text-shadow: 1px 1px 1px rgba(0, 0, 0, 0.2);
	text-transform: uppercase;
	font-family: $heading-font;

	@media (min-width: $break-large) {
		padding: 10px 9px;
	}

	@media (min-width: 1050px) {
		padding: 10px 12px;
	}

	@media (min-width: $break-xlarge) {
		padding: 10px 15px;
	}
}

/* Top level hover state, preserve hover state when hovering dropdown  */
.main-nav li > a:hover,
.main-nav li > a:focus,
.main-nav li:focus > a,
.main-nav li:hover > a {
	color: #fff;
	outline: 0;
}

