/* Dropdowns */
.main-nav ul {
	position: absolute;
	top: auto;
	left: -9999px;
	margin-left: -100px;
	opacity: 0;
	-webkit-transition: opacity 0.2s linear;
	transition: opacity .2s linear;
	min-width: 200px;
	background: #fff;
	border-top: 4px solid $teal;
	box-shadow: 0 3px 10px rgba(#333, 0.2);
}

/* Arrow */
.main-nav ul:before {
	content: "";
	display: block;
	height: 10px;
	left: 50%;
	margin-left: -10px;
	position: absolute;
	top: -10px;
	width: 10px;
	z-index: 1;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	border-bottom: 10px solid $teal;
}

.main-nav li:hover ul,
.main-nav li:focus ul,
ul.show-menu {
	left: 50%;
	opacity: 1;
	z-index: 99;
}

.main-nav ul li {
	border-top: 1px solid #e5e5e5;
	float: none;
	position: static;
}

.main-nav ul li:before, .main-nav ul li.last:after {
	display: none;
}

.main-nav ul li:first-of-type {
	border-top: none;
}

.main-nav ul a {
	color: #333;
	float: none;
	display: block;
	font-size: 13px;
	font-weight: bold;
	letter-spacing: 0;
	line-height: 1.3;
	padding: 15px 15px;
	text-align: left;
	text-transform: none;
}

.main-nav ul a:hover,
.main-nav ul a:focus,
.main-nav ul li.active a {
	background-color: #fafafa;
	color: $link-color;
}

.sec-nav {
	@extend .main-nav;

	@media (min-width: $break-large) {
		text-align: right;
	}

	.stream-link {
		background: #ffffff;
		border-radius: 0;
		color: black;
		text-shadow: none;
		box-shadow: rgba(0, 0, 0, 0.17) 1px 1px 2px 2px;

		//border: 2px solid black;

		i {
			color: $red;
			padding-left: 4px;
		}

		&:hover {
			color: $brand-primary;
			background: #eee;
		}

		&:active {
			background: #ddd;

			//box-shadow: black 1px 3px 0px;
	
		}
	}
}

/* Mobile Toggle */
.menu-icon {
	text-indent: 38px;
	width: 50px;
	height: 50px;
	display: block;
	line-height: 50px;
	padding: 0;
	color: white;
	transform: translate3d(0, 0, 0);
	text-decoration: none;
	cursor: pointer;
	position: absolute;
	top: 16px;
	right: 50px;
}

.menu-icon span::after {
	content: "";
	position: absolute;
	display: block;
	height: 0;
	top: 50%;
	margin-top: -7px;
	left: 13px;
	box-shadow: 0 0px 0 1px white, 0 7px 0 1px white, 0 14px 0 1px white;
	width: 17px;
}