.post .sidebar {
	.cat-icon-container {
		float: none;
	}
}

.home .sidebar, .archive .sidebar {
	.sidebar-cat.sponsors {
		padding-top: 0;
	}
}

.sidebar {
	@media (min-width: $screen-lg-min) {
		//position: sticky;
		//top: 0;
	}

	section h1, & > h2 {
	
		font-weight: bold;
		text-transform: uppercase;
		text-align: center;
		font-size: 18px;
		.cat-icon-container {
			padding: 10px 0;
		}
	}

	a.sponsor-img {
		display: block;
		width: 100%;
		padding: 15px 0;

		img {
			width: 55%;
			display: block;
			margin: auto;
		}
	}

	.post {}

	.sidebar-cat {
		h1 {
			padding-top: 5px;

			//border-top: 3px solid $brand-primary;
	
		}

		&.music h1 {
			border-color: $teal;

			a {
				color: $teal;
			}
		}

		&.column h1 {
			border-color: $teal;

			a {
				color: $teal;
			}
		}

		&.column .post .entry-title {
			border-color: $teal;
		}

		&.music .post .entry-title {
			border-color: $teal;
		}

		&.news h1 {
			border-color: $brand-primary;

			a {
				color: $brand-primary;
			}
		}

		&.news .post .entry-title {
			border-color: $brand-primary;
		}

		&.sports h1 {
			border-color: #000;

			a {
				color: #000;
			}
		}

		&.sports .post .entry-title {
			border-color: #000;
		}

		&.sponsors {
			padding-top: 50px;
			padding-bottom: 50px;
		}
	}

	article {
		img.main-image {
			padding-bottom: 5px;
		}

		.entry-title {
			margin-top: 0;
			border: none;
			padding-top: 0;
			font-size: 25px;

			@media (min-width: $screen-sm-min) {
				font-size: 18px;
			}
		}
	}
}